#root {
  height: 100%;
  width: 100%;
}

.App {
  height: 100%;
  width: 100%;
  position: relative;
  /* text-align: center;
  min-height: 100%; */
}

.App-header {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  z-index: -10;
  background-color: #222;
  background-image: url('./img/Madison0/bg0-blur.jpg');

  display: grid;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.App-img {
  height: 240px;
  width: 240px;
  border-radius: 100%;
  margin-top: -60px;
  border: 3px solid #fff
}

.App-title {
  color: #fff;
  opacity: .9;
  text-align: center;
}

.App-title h1 {
  padding: 0;
  margin: 0;
  font-size: 4.5em;
  font-weight: 700;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3)
}

.icon {
  opacity: .4;
  color: #fff
}

.icon:hover {
  opacity: .8;
}

i.fa-linkedin:hover {
  color: #0077b5
}

i.fa-github:hover {
  color: #333
}

i.fa-quora:hover {
  color: #A20
}

i.fa-envelope:hover {
  color: #0072C6
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



